import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import Form from 'react-bootstrap/Form';
import BasicLayout from '../../layouts/BasicLayout';

function NewHostPage() {
    const [newMember, setNewMember] = useState({
        hostNum: '',
        password: '',
        hostName: '',
        phoneNum: '',
    });
    const [isBusinessValid, setIsBusinessValid] = useState(false);
    const navigate = useNavigate();

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setNewMember((prevNewMember) => ({
            ...prevNewMember,
            [name]: value,
        }));
    };

    const checkBusinessValidity = async () => {
        try {
            const response = await axios.post('jNJwXhsGE%2BR4ssYCiosHjGYR%2FzyXPcxd0kTjAMRluKEZ7UNjAf0G37HlXVE5YGQBs5LgDaSfEdI1K%2F%2B3%2Buf1rQ%3D%3D', {
                b_no: [newMember.hostNum],
            });
            const valid = response.data.data[0]['b_stt_cd'] === '01';
            setIsBusinessValid(valid);
        } catch (error) {
            console.error('Error checking business validity:', error);
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (!isBusinessValid) {
            alert('사업자 인증에 실패했습니다.');
            return;
        }

        // 나머지 유효성 검사 및 회원가입 처리
        // ...

        try {
            const response = await axios.post('/api/hostNew', {
                hostNum: newMember.hostNum,
                password: newMember.password,
                hostName: newMember.hostName,
                phoneNum: newMember.phoneNum,
            });

            if (response.data.alertMessage) {
                alert(response.data.alertMessage);
            }

            if (response.data.successMessage) {
                localStorage.setItem('hostName', newMember.hostName);
                navigate('/host/login');
            }
        } catch (error) {
            console.error('Error submitting form:', error);
        }
    };

    useEffect(() => {
        if (newMember.hostNum) {
            checkBusinessValidity();
        }
    }, [newMember.hostNum]);

    return (
        <BasicLayout>
            <div className="loginBox">
                <h1>
                    <b>회원가입</b>
                </h1>
                <Form onSubmit={handleSubmit}>
                    <span className="formName">사업자번호</span>
                    <input
                        className="formInfo"
                        type="text"
                        placeholder="사업자번호를 입력해주세요"
                        name="hostNum"
                        onChange={handleInputChange}
                        value={newMember.hostNum}
                    />

                    <span className="formName">비밀번호</span>
                    <input
                        className="formInfo"
                        type="password"
                        placeholder="비밀번호를 입력해주세요"
                        name="password"
                        onChange={handleInputChange}
                        value={newMember.password}
                    />

                    <span className="formName">상호명</span>
                    <input
                        className="formInfo"
                        type="text"
                        placeholder="상호명을 입력해주세요"
                        name="hostName"
                        onChange={handleInputChange}
                        value={newMember.hostName}
                    />

                    <span className="formName">전화번호</span>
                    <input
                        className="formInfo"
                        type="text"
                        placeholder="전화번호를 입력해주세요"
                        name="phoneNum"
                        onChange={handleInputChange}
                        value={newMember.phoneNum}
                    />

                    <div className="loginBtn">
                        <button type="submit">회원가입</button>
                    </div>
                </Form>
            </div>
        </BasicLayout>
    );
}

export default NewHostPage;
